
export function contentIntroLeft(selectData,theme){
  let list = ''
  switch(theme){
    case 'Activity' :
        const startTime = selectData.StartTime.split('T')
        const startTimeHour = startTime[1].split('+')[0].substr(0,5)
        const endTime = selectData.EndTime.split('T')
        const endTimeHour = endTime[1].split('+')[0].substr(0,5)
        const time = startTime[0].split('-').join('/') + ' ' +startTimeHour+' ~ ' + endTime[0].split('-').join('/') + ' ' +endTimeHour
        list = /*html*/`
        <ul>
            <li class='d-flex'>
            <strong class='flex-shrink-0'> :</strong><span class='ms-1'>${time}</span>
            </li>
            <li class='d-flex'>
            <strong  class='flex-shrink-0'> :</strong>
            <a class='ms-1 link-secondary' href="tel:${selectData.Phone}">${selectData.Phone||''}</a>
            </li>
            <li class='d-flex'>
            <strong class='flex-shrink-0'> :</strong><span class='ms-1'>${selectData.Organizer||''}</span>
            </li>
            <li class='d-flex'>
            <strong class='flex-shrink-0'> :</strong><span class='ms-1'>${selectData.Address||selectData.Location||''}</span>
            </li>
            <li class='d-flex'>
            <strong class='flex-shrink-0'> :</strong>
            <a class='ms-1 link-secondary' href="${selectData.MapUrl||selectData.WebsiteUrl||'#'}">${selectData.MapUrl||selectData.WebsiteUrl||''}</a>
            </li>
            <li class='d-flex'>
            <strong class='flex-shrink-0'> :</strong><span class='ms-1'>${selectData.Charge||''}</span>
            </li>
            <li class='d-flex'>
            <strong class='flex-shrink-0'> :</strong><span class='ms-1'>${selectData.TravelInfo||''}</span>
            </li>
        </ul>
        `
        $('.content-intro-left').html(list)
      break

    case 'ScenicSpot' :
        list = /*html*/`
        <ul>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong><span class='ms-1'>${selectData.OpenTime||''}</span>
        </li>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong><span class='ms-1'>${selectData.Organizer||''}</span>
        </li>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong>
        <a class='ms-1 link-secondary' href="tel:${selectData.Phone}">${selectData.Phone||''}</a>
        </li>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong>
        <span class='ms-1'>${selectData.Address||selectData.Location||''}</span>
        </li>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong>
        <a class='ms-1 link-secondary' target="blank" href="${selectData.MapUrl||selectData.WebsiteUrl||'#'}">${selectData.MapUrl||selectData.WebsiteUrl||''}</a>
        </li>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong><span class='ms-1'>${selectData.TicketInfo||''}</span>
        </li>
        </ul>`
        $('.content-intro-left').html(list)
      break
    case 'Restaurant' :
      
        list = /*html*/`
        <ul>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong><span class='ms-1'>${selectData.OpenTime||''}</span>
        </li>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong>
        <a class='ms-1 link-secondary' href="tel:${selectData.Phone}">${selectData.Phone||''}</a>
        </li>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong>
        <span class='ms-1'>${selectData.RestaurantName||''}</span>
        </li>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong>
        <span class='ms-1'>${selectData.Address||selectData.Location||''}</span>
        </li>
        <li class='d-flex'>
        <strong class='flex-shrink-0'> :</strong>
        <a class='ms-1 link-secondary' target="blank" href="${selectData.MapUrl||selectData.WebsiteUrl||'#'}">${selectData.MapUrl||selectData.WebsiteUrl||''}</a>
        </li>
        </ul>`
        $('.content-intro-left').html(list)
        break
      }
}